import React from "react";
import { graphql } from "gatsby";
import { Link } from "gatsby";

import Layout from "../components/layout";

export default ({ data }) => (
  <Layout>
    <h1>About {data.site.siteMetadata.title}</h1>
    <p>
      Hello im a front end web and graphic designer. My expertise is Html, Css
      and Javascript. Check out <Link to="/">my blog</Link>
      &nbsp; where I have documented things Ive learned along the way.
    </p>
  </Layout>
);

export const query = graphql`
  query {
    site {
      siteMetadata {
        title
      }
    }
  }
`;
